<template>
    <div class='recharge memberlist'>
          <el-dialog :title="text" :visible.sync="dialogFormVisible" :before-close="handleClose">
            <!-- <div class='cardText'>{{cardList.cardName}}</div> -->
            <div class='personInfo'>
                <div class='personLeft'>
                    <div class='fir'>绑定会员</div>
                    <div class='sec'><img :src="personpic"/>{{cardList.personName}}</div>
                </div>
                <div class='personRight'>
                    <div class='fir'>当前有效期</div>
                    <div class='sec' v-if='cardList.balanceList && cardList.balanceList[0]'>
                        {{cardList.balanceList[0].startTime | timeformer}}<span>到</span>{{cardList.balanceList[0].endTime | timeformer}}
                    </div>
                </div>
                <!-- <div class='personRight' v-if="cardType === 'PREPAID'">
                    <div class='fir'>当前余额</div>
                    <div class='sec'>￥{{cardList.balance}}元</div>
                </div>
                <div class='personRight' v-if="cardType === 'TIMES'">
                    <div class='fir'>当前剩余次数</div>
                    <div class='sec'>{{cardList.balance}}次</div>
                </div> -->
            </div>
            <el-form :model='form'  ref='form' :rules="rules">
                <div class='form'>
                    <div v-if="text === '会员卡退卡'">
                        <el-form-item label="退款金额：" class='formNew option returnCard' prop='amount' :class="isActive === 1 ? 'border-active' : ''">
                            <el-input v-model="form.amount" class='inputNew' placeholder="请输入" @focus="inDiv(1)" @blur="outDiv(1)" :maxlength="8">></el-input>
                        </el-form-item>
                    </div>
                    <!-- <div class='left' v-if="text === '会员卡取消停卡'">
                        <el-form-item label="有效期起：" class='formNew' prop='startTime'>
                            <el-date-picker
                                v-model="form.startTime"
                                type="date"
                                placeholder="选择日期"
                                class='inputNew'
                                @focus="inDiv(7)" 
                                @blur="outDiv(7)"
                                :picker-options="pickerOptions1">
                            </el-date-picker>
                        </el-form-item>
                        <div class='tips' v-if="cardType !== 'DATELIMIT' && cardType !== 'PACKAGE'">注：（不填则有效期为"无限期"）</div>
                    </div> -->
                    <!-- <div class='right' v-if="text === '会员卡取消停卡'">
                        <el-form-item label="实收（元）：" class='formNew option' prop='actualPay' :class="isActive === 3 ? 'border-active' : ''">
                            <el-input v-model="form.actualPay" class='inputNew' placeholder="请输入" auto-complete="off" @focus="inDiv(3)" @blur="outDiv(3)" :maxlength="8">></el-input>
                        </el-form-item>
                        <el-form-item label="有效期至：" class='formNew' prop='price'>
                            <el-date-picker
                                v-model="form.startTime"
                                type="date"
                                placeholder="选择日期"
                                class='inputNew'
                                @focus="inDiv(7)" 
                                @blur="outDiv(7)"
                                :picker-options="pickerOptions1">
                            </el-date-picker>
                        </el-form-item>
                    </div> -->
                </div>
                <!-- <div class='dayText'>{{dayText}}</div>
                <div class='deadline' v-if="cardType === 'DATELIMIT' || cardType === 'PACKAGE' ">到<span>{{deadlineDate | timeformer}}</span></div>
                <div class='deadline' v-if="cardType === 'PREPAID'"><span>￥{{parseInt(cardList.balance) + (form.amount ? parseInt(form.amount) : 0)}}元</span></div>
                <div class='deadline' v-if="cardType === 'TIMES'"><span>{{parseInt(cardList.balance) + (form.amount ? parseInt(form.amount) : 0)}}次</span></div>
                <div class='deadline' v-if="cardType === 'PREPAID' || cardType === 'TIMES'">有效期至<span>{{deadlineDate | timeformer}}</span></div> -->
                <div class='form secform'>
                    <div class='left'>
                        <el-form-item label="支付方式：" class='formNew option' prop='payMode' :class="isActive === 4 ? 'border-active' : ''" v-if="text === '会员卡退卡'">
                            <el-select v-model="form.payMode " placeholder="请选择" clearable @focus="inDiv(4)" @blur="outDiv(4)">
                                <el-option
                                v-for="(item, index) in paylist"
                                :key="index"
                                :label="item.name"
                                :value="item.value"
                                :class="{whiteColor: index % 2 !== 0 ? true : false}">
                                {{item.name}}
                                <img src='../assets/images/icon_right.png'/>
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="经办人：" class='formNew option' prop='handlerId' :class="isActive === 5 ? 'border-active' : ''" v-if="text !== '会员卡退卡'">
                            <el-select v-model="form.handlerId" placeholder="请选择" clearable @focus="inDiv(5)" @blur="outDiv(5)">
                                <el-option
                                v-for="(item, index) in salelist"
                                :key="index"
                                :label="item.coachName"
                                :value="item.coachId"
                                :class="{whiteColor: index % 2 !== 0 ? true : false}">
                                {{item.coachName}}
                                <img src='../assets/images/icon_right.png'/>
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </div>
                    <div class='right'>
                        <el-form-item label="经办人：" class='formNew option' prop='handlerId' :class="isActive === 5 ? 'border-active' : ''" v-if="text === '会员卡退卡'">
                            <el-select v-model="form.handlerId" placeholder="请选择" clearable @focus="inDiv(5)" @blur="outDiv(5)">
                                <el-option
                                v-for="(item, index) in salelist"
                                :key="index"
                                :label="item.coachName"
                                :value="item.coachId"
                                :class="{whiteColor: index % 2 !== 0 ? true : false}">
                                {{item.coachName}}
                                <img src='../assets/images/icon_right.png'/>
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </div>
                </div>
                <el-form-item label="原因：" class='formNew TextArea' :class="isActive === 6 ? 'border-active' : ''">
                    <el-input v-model="form.note" class='inputNew'  type="textarea" placeholder="请输入" @focus="inDiv(6)" @blur="outDiv(6)" :maxlength="255"></el-input>
                </el-form-item>
                <div class='bot' v-if="text === '会员卡停卡'">
                    <div class='text'>已停卡次数：<span>{{num}}次</span></div>
                    <div class='tips'>注：(最多可停卡2次)</div>
                </div>
            </el-form>
            <div slot='footer' class='dialog-footer'>
                <el-button @click="resetForm('form')">取消</el-button>
                <el-button type='primary' @click="submit('form')">确定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import {getbranchcoach, recharge, downMedia, backCard, stopCard, stopCardNum, nostopCardNum} from '../services/service.js'
import {isApiResponseOk, getSessionStorage} from '../utils/com.js'
export default {
    data () {
        return {
            form: {
                name: '',
                region: ''
            },
            formLabelWidth: 100,
            cardList: {},
            personpic: require('../assets/images/img_user_pic_deflut.png'),
            isActive: -1,
            paylist: [
                {name: '微信', value: 'WXPAY'},
                {name: '支付宝', value: 'ALIPAY'},
                {name: '现金', value: 'CASH'},
                {name: '刷卡', value: 'SLOTCARD'},
                {name: '转账', value: 'TRANSFER'}
            ],
            salelist: [],
            rules: {
                openCardByBranchId: [{required: true, message: '门店不能为空', trigger: 'change'}],
                personName: [{required: true, message: '会员姓名不能为空', trigger: 'change'}],
                vipCardType: [{required: true, message: '会员卡类型不能为空', trigger: 'blur'}],
                cardTypeInfoCode: [{required: true, message: '会员卡种类不能为空', trigger: 'change'}],
                amount: [
                {required: true, message: '退款金额不能为空', trigger: 'blur'}
                ],
                actualPay: [
                {required: true, message: '充值金额不能为空', trigger: 'blur'},
                {type: 'number', message: '金额必须为数字类型'}
                ],
                startTime: [{required: true, message: '开始时间不能为空', trigger: 'blur'}],
                payMode: [{required: true, message: '支付方式不能为空', trigger: 'change'}],
                handlerId: [{required: true, message: '经办人不能为空', trigger: 'change'}]
            },
            num: 0,
            pickerOptions1: {
              disabledDate(time) {
                return time.getTime() < Date.now() - 8.64e7;
              }
            },
        }
    },
    props: ['dialogFormVisible', 'text', 'cardType'],
    methods: {
        inDiv: function (data) {
            this.isActive = data
        },
        outDiv: function (data) {
            this.isActive = -1
        },
        async downMedia () {
            if (!this.cardList.headImage) return
            if (this.cardList.headImage.indexOf('http') > -1) {
                this.personpic = this.cardList.headImage
            } else {
                let data = {
                    mediaId:  this.cardList.headImage
                }
                const res = await downMedia(data)
                this.personpic = res
            }
        },
        handleClose () {
            let self = this
            this.$confirm('确认关闭？', {type: 'warning'})
                .then(_ => {
                self.resetForm('form')
                self.$emit('closeForm', close)
                })
                .catch(_ => {})
        },
        resetForm(formName) {
            this.$refs[formName].resetFields()
            this.$emit('closeForm', close)
        },
        // 获取所有的教练
        async getallcoach () {
            const res = await getbranchcoach({branchId: ''})
            if (isApiResponseOk(res)) {
                this.salelist = res.data
            }
        },
        submit(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    // console.log('数据', this.form)
                    // this.cardList.userCardCode
                    // Object.assign(this.form, )
                    console.log('用户数据', this.cardList, this.form)
                    if (this.text === '会员卡退卡') {
                        this.backCard(this.cardList.personId, this.cardList.code, this.form)
                    } else if (this.text === '会员卡停卡'){
                        this.stopCard(this.cardList.personId, this.cardList.code, this.form)
                    } else if (this.text === '会员卡取消停卡'){
                        this.nostopCardNum(this.cardList.personId, this.cardList.code, this.form)
                    }
                } else {

                }
            })
        },
        // 退卡操作
        async backCard (personId, userCardCode, data) {
            const res = await backCard(personId, userCardCode, data.amount, data.payMode, data.handlerId, data.note)
            if (isApiResponseOk(res)) {
                this.$message({
                    showClose: true,
                    message: '退卡成功',
                    type: 'success'
                })
                this.resetForm('form')
            }
        },
        // 停卡操作
        async stopCard (personId, userCardCode, data) {
            const res = await stopCard(personId, userCardCode, data.handlerId, data.note)
            if (isApiResponseOk(res)) {
                this.$message({
                    showClose: true,
                    message: '停卡成功',
                    type: 'success'
                })
                this.resetForm('form')
            }
        },
        // 获取停卡次数
        async stopCardNum () {
            const res = await stopCardNum(this.cardList.code)
            if (isApiResponseOk(res)) {
               this.num = res.data 
            }
        }, 
        // 取消停卡
        async nostopCardNum (personId, userCardCode, data) {
            const res = await nostopCardNum(personId, userCardCode, data.handlerId, data.note)
            if (isApiResponseOk(res)) {
                this.$message({
                    showClose: true,
                    message: '取消停卡成功',
                    type: 'success'
                })
                this.resetForm('form')
            }
        }

    },
    watch: {
        'dialogFormVisible': function (newVal) {
            if (newVal) {
                this.form = {}
                this.cardList = this.$store.state.person
                if (this.cardList.balanceList[0]) {
                    this.deadlineDate = this.cardList.balanceList[0].endTime
                }
                if (this.text === '会员卡停卡') {
                    this.stopCardNum()
                }
                this.getallcoach()
                console.log('数据', this.cardList)
                this.downMedia ()
            }
        }
    }
}
</script>
<style lang="scss">
.recharge{
    .el-dialog{
        width: 6.6rem;
        .el-dialog__header{
            height: .6rem;
            /* line-height: .6rem; */
            padding-left: .3rem;
            padding-top: .2rem;
            border-bottom: 1px solid #EAEEF0;
            font-size: 20px;
            color: #263238;
        }
    }
    .el-dialog__body{
        padding: 60px 40px 0 50px;
        // .cardText{
        //     font-size: 18px;
        //     color: #263238;
        //     text-align: center;
        // }
        .personInfo{
            display: flex;
            // margin-top: .4rem;
            .personLeft{
                flex: 1;
                text-align: center;
                position: relative;
                &:after{
                    content: "";
                    top: 12px;
                    position: absolute;
                    right: 0px;
                    width: 1px;
                    height: .26rem;
                    background: #E9EDEF;
                }
                img{
                    width: .2rem;
                    vertical-align: middle;
                    margin-right: .1rem;            
                    border-radius: .2rem;
                    height: .2rem;
                }
            }
            .personRight{
                flex: 1;
                text-align: center;
            }
            .fir{
                font-size: .13rem;
                color: #909399;
                margin-bottom: .13rem;
            }
            .sec{
                font-size: .17rem;
                color: #263238;
                span{
                    font-size: .14rem;
                    color: #C0C4CC;
                    margin-left: .1rem;
                    margin-right: .1rem;
                }
            }
        }
        .form{
            border-bottom: 0px !important; 
        }
        .secform{
            margin-top: 0rem !important;
        }
        .bot{
            display: flex;
            justify-content: flex-end;
            flex-flow: column;
            font-family: PingFangSC-Regular;
            font-size: .14rem;
            color: #8597A1;
            text-align: right;
            margin-bottom: .3rem;
            // letter-spacing: 0;
            // line-height: 14px;
            .text{
               line-height: .14rem; 
            }
            .tips{
                font-family: PingFangSC-Medium;
                font-size: .12rem;
                color: #C0C4CC;
                letter-spacing: 0;
                line-height: .12rem;
                margin-top: .08rem;
            }
        }

    }
    .el-input__prefix{
        left: auto;
        right: 0px;
        top: -5px;
    }
}
.memberlist .table .el-col-4:nth-child(4){
    float: inherit;
}
</style>



