<template>
    <div class='recharge'>
          <el-dialog title="会员卡充值" :visible.sync="dialogVisible" :before-close="handleClose">
            <div class='cardText'>{{cardList.cardName}}</div>
            <div class='personInfo'>
                <div class='personLeft'>
                    <div class='fir'>绑定会员</div>
                    <div class='sec'><img :src="personpic"/>{{cardList.personName}}</div>
                </div>
                <div class='personRight' v-if="cardType === 'DATELIMIT' || cardType === 'PACKAGE'">
                    <div class='fir'>当前有效期</div>
                    <div class='sec' v-if='cardList.balanceList[0]'>{{cardList.balanceList[0].startTime | timeformer}}<span>到</span>{{cardList.balanceList[0].endTime | timeformer}}</div>
                </div>
                <div class='personRight' v-if="cardType === 'PREPAID'">
                    <div class='fir'>当前余额</div>
                    <div class='sec'>￥{{cardList.balance}}元</div>
                </div>
                <div class='personRight' v-if="cardType === 'TIMES'">
                    <div class='fir'>当前剩余次数</div>
                    <div class='sec'>{{cardList.balance}}次</div>
                </div>
            </div>
            <el-form :model='form' :rules="rules" ref='form'>
                <div class='form'>
                    <div class='left'>
                        <!-- <el-form-item :label="实体卡号" class='formNew option' prop='amount' :class="isActive === 1 ? 'border-active' : ''" v-if="cardType === 'PACKAGE'">
                            <el-input v-model="form.amount" class='inputNew' placeholder="请输入" @focus="inDiv(1)" @blur="outDiv(1)" :maxlength="8">></el-input>
                        </el-form-item> -->
                        <el-form-item :label="labelText" class='formNew option' prop='amount' :class="isActive === 1 ? 'border-active' : ''">
                            <el-input v-model="form.amount" class='inputNew' placeholder="请输入" @focus="inDiv(1)" @blur="outDiv(1)" :maxlength="8">></el-input>
                        </el-form-item>
                        <el-form-item label="设置有效期（天）：" class='formNew day' v-if="cardType !== 'DATELIMIT' && cardType !== 'PACKAGE'" :class="isActive === 2 ? 'border-active' : ''">
                            <el-input v-model="form.dayLimit" class='inputNew' placeholder="请输入" @focus="inDiv(2)" @blur="outDiv(2)" :maxlength="8"></el-input>
                        </el-form-item>
                        <div class='tips' v-if="cardType !== 'DATELIMIT' && cardType !== 'PACKAGE'">注：（不填则有效期为"无限期"）</div>
                    </div>
                    <div class='right'>
                        <el-form-item label="实收（元）：" class='formNew option' prop='actualPay' :class="isActive === 3 ? 'border-active' : ''">
                            <el-input v-model="form.actualPay" class='inputNew' placeholder="请输入" auto-complete="off" @focus="inDiv(3)" @blur="outDiv(3)" :maxlength="8">></el-input>
                        </el-form-item>
                        <!-- <el-form-item label="开始时间：" class='formNew' prop='price'>
                            <el-input v-model.number="form.price" class='inputNew' placeholder="请输入" auto-complete="off" :maxlength="5"></el-input>
                        </el-form-item> -->
                        <div class='timePer' v-if="cardType === 'TIMES'">充值单价：{{timePer | fundFormat(1)}}元/次</div>
                    </div>
                </div>
                <div class='dayText'>{{dayText}}</div>
                <div class='deadline' v-if="cardType === 'DATELIMIT' || cardType === 'PACKAGE' ">到<span>{{deadlineDate | timeformer}}</span></div>
                <div class='deadline' v-if="cardType === 'PREPAID'"><span>￥{{parseInt(cardList.balance) + (form.amount ? parseInt(form.amount) : 0)}}元</span></div>
                <div class='deadline' v-if="cardType === 'TIMES'"><span>{{parseInt(cardList.balance) + (form.amount ? parseInt(form.amount) : 0)}}次</span></div>
                <div class='deadline' v-if="cardType === 'PREPAID' || cardType === 'TIMES'">有效期至<span>{{deadlineDate | timeformer}}</span></div>
                <div class='form secform'>
                    <div class='left'>
                        <el-form-item label="支付方式：" class='formNew option' prop='paymentMode' :class="isActive === 4 ? 'border-active' : ''">
                            <el-select v-model="form.paymentMode " placeholder="请选择" clearable @focus="inDiv(4)" @blur="outDiv(4)">
                                <el-option
                                v-for="(item, index) in paylist"
                                :key="index"
                                :label="item.name"
                                :value="item.value"
                                :class="{whiteColor: index % 2 !== 0 ? true : false}">
                                {{item.name}}
                                <img src='../../assets/images/icon_right.png'/>
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </div>
                    <div class='right'>
                        <el-form-item label="销售：" class='formNew option' prop='salesperson' :class="isActive === 5 ? 'border-active' : ''">
                            <el-select v-model="form.salesperson" placeholder="请选择" clearable @focus="inDiv(5)" @blur="outDiv(5)">
                                <el-option
                                v-for="(item, index) in salelist"
                                :key="index"
                                :label="item.name"
                                :value="item.id"
                                :class="{whiteColor: index % 2 !== 0 ? true : false}">
                                {{item.name}}
                                <img src='../../assets/images/icon_right.png'/>
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </div>
                </div>
                <el-form-item label="备注：" class='formNew TextArea' :class="isActive === 6 ? 'border-active' : ''">
                    <el-input v-model="form.note" class='inputNew'  type="textarea" placeholder="请输入" @focus="inDiv(6)" @blur="outDiv(6)" :maxlength="255"></el-input>
                </el-form-item>
            </el-form>
            <div slot='footer' class='dialog-footer'>
                <el-button @click="resetForm('form')">取消</el-button>
                <el-button type='primary' @click="submit('form')">确定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import {getbranchcoach, recharge, downMedia, addDeadline, getAllsale} from '../../services/service.js'
import {isApiResponseOk, getSessionStorage} from '../../utils/com.js'
import { debug } from 'util';
export default {
  data () {
    return {
      form: {
        amount: '',
        actualPay: '',
        note: '',
        paymentMode: '',
        salesperson: '',
        dayLimit: ''
      },
      rules: {
        amount: [
          {required: true, message: '充值不能为空', trigger: 'blur'}
        ],
        actualPay: [
          {required: true, message: '实收不能为空', trigger: 'blur'}
        ],
        salesperson : [
          {required: true,  message: '销售不能为空', trigger: 'change'}
        ],
        paymentMode : [
          {required: true, message: '支付方式不能为空', trigger: 'change'}
        ]
      },
      branchlist: [],
      paylist: [
        {name: '微信', value: 'WXPAY'},
        {name: '支付宝', value: 'ALIPAY'},
        {name: '现金', value: 'CASH'},
        {name: '刷卡', value: 'SLOTCARD'},
        {name: '转账', value: 'TRANSFER'}
      ], // 支付方式列表
      hide: false,
      salelist: [],
      dayText: '充值后余额',
      labelText: '充值（元）：',
      datelimit: false,
      times: false,
      prepaid: true,
      balance: '',
      cardList: {},
      personpic: require('../../assets/images/img_user_pic_deflut.png'),
      deadlineDate: '',
      isActive: -1
    }
  },
  props: ['dialogVisible', 'cardType'],
  created () {
    // 获取所有的教练即销
    this.getallcoach()
    // 获取图片
    // PREPAID: 储值卡， TIMES: 次卡， DATELIMIT：期限卡
  },
  watch: {
    dialogVisible: function (newVal) {
        if (newVal) {
          this.form = {}
          this.cardList = this.$store.state.person
          console.log('12222', this.cardList)
          if (this.cardList.balanceList[0]) {
            this.deadlineDate = this.cardList.balanceList[0].endTime
          }
          this.downMedia ()
          switch (this.cardType) {
            case 'PREPAID':
                this.dayText = '充值后余额'
                this.labelText = '充值（元）：'
                this.prepaid = true
                this.times = false
                this.datelimit = false
                return
            case 'TIMES':
                this.dayText = '充值后次数'
                this.labelText = '充值（次）：'
                this.prepaid = false
                this.times = true
                this.prepaid = false
                return
            case 'DATELIMIT':
                this.dayText = '充值后有效期'
                this.labelText = '充值（天）：'
                this.prepaid = false
                this.times = false
                this.datelimit = true
                return
            case 'PACKAGE':
                this.dayText = '充值后有效期'
                this.labelText = '充值（天）：'
                this.prepaid = false
                this.times = false
                this.datelimit = true
          }
        }
    },
    // 动态设置有效期
    'form.amount': function (newVal, oldVal) {
        let FormatReg = /^\d+$/
        if (newVal < 0) return
        if (this.form.amount && this.form.amount.length) {
          if (FormatReg.test(newVal)) {
            this.form.amount = newVal
          } else {
            setTimeout(() => {
              this.form.amount = oldVal
            }, 1)
          }
        }
        if (!newVal) {
          this.deadlineDate = this.cardList.balanceList[0].endTime
          return
        }
        if (this.cardType === 'DATELIMIT' || this.cardType === 'PACKAGE') {
          // let date1 = new Date(this.deadlineDate)
          // date1.setDate(date1.getDate() + parseInt(newVal))
          // this.deadlineDate = date1
          this.addDeadline(newVal)
        }
    },
    'form.dayLimit': function (newVal, oldVal) {
        let FormatReg = /^\d+$/
        if (newVal < 0) return
        if (this.form.dayLimit && this.form.dayLimit.length) {
          if (FormatReg.test(newVal)) {
            this.form.dayLimit = newVal
          } else {
            setTimeout(() => {
              this.form.dayLimit = oldVal
            }, 1)
          }
        }
        if (!newVal) {
          this.deadlineDate = this.cardList.balanceList[0].endTime
          return
        }
        if (this.cardType !== 'DATELIMIT' || this.cardType !== 'PACKAGE') {
          // let date1 = new Date(this.deadlineDate)
          // date1.setDate(date1.getDate() + parseInt(newVal))
          // this.deadlineDate = date1
          this.addDeadline(newVal)
        }
    },
    'form.actualPay': function (newVal, oldVal) {
        let FormatReg = /^\d+$/
        if (newVal < 0) return
        if (this.form.actualPay && this.form.actualPay.length) {
          if (FormatReg.test(newVal)) {
            this.form.actualPay = newVal
          } else {
            setTimeout(() => {
              this.form.actualPay = oldVal
            }, 1)
          }
        }
    }
  },
  computed: {
    // 计算属性的 getter
    timePer: function () {
      // `this` 指向 vm 实例
      let res = 0
      if (this.cardType && this.cardType=== 'TIMES'){
        if (this.form.actualPay && this.form.amount) {
          res = this.form.actualPay / this.form.amount
        }
      }
      return res
    }
  },
  methods: {
    // 获取所有的教练
    async getallcoach () {
      const res = await getAllsale()
      if (isApiResponseOk(res)) {
        this.salelist = res.data
        console.log('获取到的所有的教练', res)
      }
    },
    handleClose () {
      let self = this
      this.$confirm('确认关闭？', {type: 'warning'})
        .then(_ => {
          self.resetForm('form')
          self.$emit('close', close)
        })
        .catch(_ => {})
    },
    // 提交信息
    submit (ruleForm) {
      let self = this
      this.$refs[ruleForm].validate((valid) => {
        if (valid) {
          self.form.userCardCode = this.cardList.code
          self.form.vipCardType = this.cardList.vipCardType
          self.form.personId = this.cardList.personId
          self.form.dayLimit = parseInt(self.form.dayLimit)
          recharge(self.form).then(function (res) {
            if (isApiResponseOk(res)) {
              self.$message({
                showClose: true,
                message: '续卡成功',
                type: 'success'
              })
              self.resetForm('form')
              self.$emit('close', close)
            }
          })
        }
      })
    },
    async downMedia () {
        if (!this.cardList.headImage) return
        if (this.cardList.headImage.indexOf('http') > -1) {
            this.personpic = this.cardList.headImage
        } else {
            let data = {
                mediaId:  this.cardList.headImage
            }
            const res = await downMedia(data)
            this.personpic = res
            console.log('获取的图片', res)
        }
    },
    inDiv: function (data) {
      this.isActive = data
    },
    outDiv: function (data) {
      this.isActive = -1
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.$emit('close', close)
    },
    // 充值有效期
    async addDeadline (day) {
      console.log('卡号', this.cardList, this.cardList.card)
      const res = await addDeadline(this.cardList.code, day)
      // this.deadlineDate
      if (isApiResponseOk(res) && res.data) {
        this.deadlineDate = res.data
      }
    }
  }
}
</script>
<style lang="sass" scoped>
.recharge
        .el-dialog__body
            padding: 50px 40px 0 50px;
            .cardText
                font-size: 18px;
                color: #263238;
                text-align: center;
            .personInfo
                display: flex;
                margin-top: .4rem;
                .personLeft
                    flex: 1;
                    text-align: center;
                    position: relative;
                    &:after
                        content: "";
                        top: 12px;
                        position: absolute;
                        right: 0px;
                        width: 1px;
                        height: .26rem;
                        background: #E9EDEF;
                    img
                        width: .2rem;
                        vertical-align: middle;
                        margin-right: .1rem;            
                        border-radius: .2rem;
                        height: .2rem;
                .personRight
                    flex: 1;
                    text-align: center;
                .fir
                    font-size: .13rem;
                    color: #909399;
                    margin-bottom: .13rem;
                .sec
                    font-size: .17rem;
                    color: #263238;
                    span
                        font-size: .14rem;
                        color: #C0C4CC;
                        margin-left: .1rem;
                        margin-right: .1rem;
            .timePer
                font-size: .11rem;
                font-family: PingFangSC-Regular;
                color: #8597A1;
                float: right;
                margin-top: -.14rem;
                margin-bottom: 0.2rem;
</style>





