<template>
    <div class='coachlist memberlist'>
        <!-- <div class='addMember' @click='addMember'>
            <img src='../../assets/images/btn_add.png'>
            <span>添加会员卡</span>
        </div> -->
        <div class='top'>
          <div class='title'>会员卡管理 <span>{{total}}</span></div>
          <div class='btn add' @click='addMember'>
              <i class='iconfont icon-btn_icon_add'></i>
              <span>添加会员卡</span>
          </div>
          <div class='btn' @click='member'><i class='iconfont icon-btn_icon_input'></i>导入会员卡</div>
        </div>
        <Table  :tableData="tableData" :tableQuery="tableQuery" :tabhide="tabhide" :tableKey="tableKey" ref="Table" :total="total" :isPage = "isPage" :pageSize = "pageSize" v-on:getQueryParam = "getQueryParam"
       v-on:changeVal="changeParam" v-on:getRowdata="getRowdata" :operWidth="operWidth"  :tabTxet='tabTxet'></Table>
       <card :dialogVisible="dialogVisible" :cardType="cardType" v-on:close ="close"></card>
       <memberCard :dialogFormVisible="CarddialogVisible" :text="cardText" :cardType="cardTypeMember" v-on:closeForm = "closeForm"></memberCard>
    </div>
</template>
<script>
import Table from '../../components/Table'
import Card from '../rechargeCard'
import memberCard from '@/components/memberCard'
import {member, queryCardList} from '../../services/service.js'
import {isApiResponseOk, setSessionStorage} from '../../utils/com.js'
export default {
  data () {
    return {
      tableQuery: [
        {
          name: 'physicalCardNo',
          title: '输入实体卡号',
          type: 'input',
          model: '',
          text: '会员实体卡号：'
        },
        {
          name: 'personName',
          title: '输入会员姓名',
          type: 'input',
          model: '',
          text: '会员姓名：',
          style: {float: 'inherit'}
        },
        {
          name: 'vipCardType',
          type: 'select',
          model: '',
          title: '请选择',
          text: '会员卡类型：',
          selectArr: [],
          style: {width: '67%'}
        },
        {
          name: 'status',
          type: 'select',
          model: '',
          title: '请选择',
          text: '会员卡状态：',
          selectArr: [],
          style: {width: '67%'}
        },
        {
          name: 'START_DATE',
          type: 'start_date',
          model: '',
          title: '选择开始日期',
          format: 'yyyy-MM-dd HH:mm:ss',
          text: '创建时间：'
        },
        {
          name: 'END_DATE',
          type: 'end_date',
          model: '',
          title: '选择结束日期',
          format: 'yyyy-MM-dd HH:mm:ss',
          text: '到'
        }
      ],
      tableKey: [
        {name: '会员卡ID', value: 'code', nor: true},
        {name: '会员卡名称', value: 'cardName', nor: true},
        {name: '类型', value: 'vipCardType', nor: true},
        {name: '绑定会员', value: 'personName', nor: true},
        {name: '失效日期', value: 'endTime', nor: true},
        {name: '余额', value: 'balance', balance: true},
        {name: '会员卡状态', value: 'status', nor: true},
        {name: '创建日期', value: 'createTime', nor: true},
        {name: '操作', value: 'btnGroup', btn: true}
      ],
      tableData: [
      ],
      isPage: true,
      total: 0,
      tabhide: false,
      pageSize: 10,
      para: {pageNo: 1, pageSize: 10},
      // operateBtn: [{text: '详情'},{text: '续卡'}],
      operWidth: '180',
      dialogVisible: false, // 控制弹出框隐藏或者显示
      cardType: 'PREPAID',
      cardTypeMember: 'PREPAID',
      tabTxet: true,
      CarddialogVisible: false, // 控制退卡，停卡，取消停卡的弹框
      cardText: ''
    }
  },
  components: {
    Table,
    Card,
    memberCard
  },
  created () {
    // 获取学员列表
    this.$nextTick(_ => {
      this.listSelect()
      this.initCardList(this.para)
    })
    // this.initStudent(this.para)
  },
  methods: {
    // 下拉框赋值
    listSelect () {
      let option1 = [
        {
          value: '',
          label: '全部'
        },
        {
          value: 'PREPAID',
          label: '储值卡'
        },
        {
          value: 'TIMES',
          label: '次卡'
        },
        {
          value: 'DATELIMIT',
          label: '会籍卡'
        },
        {
          value: 'PACKAGE',
          label: '期限卡'
        }
      ]
      let option2 = [
        {
          value: '',
          label: '全部'
        },
        {
          value: 'NORMAL',
          label: '正常'
        },
        {
          value: 'INACTIVATION',
          label: '未激活'
        },
        {
          value: 'STOP',
          label: '停卡'
        },
        {
          value: 'CANCEL',
          label: '退卡'
        }
      ]
      this.$refs.Table.reloadValue('vipCardType', option1)
      this.$refs.Table.reloadValue('status', option2)
    },
    getQueryParam (queryObj) {
      console.log('获取参数', queryObj)
      // this.currentList = []
      // this.IndexLsit = []
      this.para.pageNo = 1
      if (queryObj.physicalCardNo) {
        this.para.physicalCardNo = queryObj.physicalCardNo
      } else {
        delete this.para.physicalCardNo
      }
      if (queryObj.personName) {
        this.para.personName = queryObj.personName
      } else {
        delete this.para.personName
      }
      if (queryObj.vipCardType) {
        this.para.vipCardType = queryObj.vipCardType
      } else {
        delete this.para.vipCardType
      }
      if (queryObj.status) {
        this.para.status = queryObj.status
      } else {
        delete this.para.status
      }
      if (queryObj.START_DATE) {
        this.para.beginTime = queryObj.START_DATE
      } else {
        delete this.para.beginTime
      }
      if (queryObj.END_DATE) {
        this.para.endTime = queryObj.END_DATE
      } else {
        delete this.para.endTime
      }
      this.initCardList(this.para)
    },
    changeParam (data) {
      this.para.pageNo = data
      this.initCardList(this.para)
    },
    getRowdata (data) {
      if (data.index === '详情') {
        this.$router.push({name: 'memberDetail', query:{personId: data.row.personId, card: data.row.code, cardName: data.row.cardName}})
      } else if (data.index === '续卡') {
        this.$store.commit('setPerson', data.row)
        this.cardType = data.row.vipCardType
        this.dialogVisible = true
      } else if (data.index === '退卡') {
        this.$store.commit('setPerson', data.row)
        this.cardTypeMember = data.row.vipCardType
        this.CarddialogVisible = true
        this.cardText = '会员卡退卡'
      } else if (data.index === '停卡') {
        this.$store.commit('setPerson', data.row)
        this.cardTypeMember = data.row.vipCardType
        this.CarddialogVisible = true
        this.cardText = '会员卡停卡'
      } else if (data.index === '取消停卡') {
        this.$store.commit('setPerson', data.row)
        this.cardTypeMember = data.row.vipCardType
        this.CarddialogVisible = true
        this.cardText = '会员卡取消停卡'
        console.log('会员卡', this.cardText)
      }
    },
    async initStudent (data) {
      const res = await member(data)
      if (isApiResponseOk(res)) {
        if (res.data) {
          this.tableData = res.data.studentSessionStubPOList
          this.total = res.data.count
          if (this.total === 0) {
            this.isPage = false
          } else {
            this.isPage = true
          }
        } else {
          this.isPage = false
        }
      } else {
        this.isPage = false
      }
    },
    // 获取所有的会员卡
    async initCardList (data) {
      const res = await queryCardList(data)
      if (isApiResponseOk(res)) {
        this.tableData = res.data.record
        this.total = res.data.totalNum
        for (let val of res.data.record) {
          if (val.status === 'STOP') {
          val['btnGroup'] = [{text: '详情'}, {text: '续卡'}, {text: '退卡'}, {text: '取消停卡'}]
          } else {
            val['btnGroup'] = [{text: '详情'}, {text: '续卡'}, {text: '退卡'}, {text: '停卡'}]
          }
        }
        if (this.total === 0) {
          this.isPage = false
        } else {
            this.isPage = true
          }
        console.log('获取数据成功', this.tableData, this.total)
      } else {
        this.isPage = false
      }
    },
    // 添加会员卡
    addMember () {
      this.$router.push({name: 'addmembercard'})
    },
    // 关闭弹出框
    close () {
      this.dialogVisible = false
      this.initCardList(this.para)
    },
    // 关闭停卡，退卡弹出框
    closeForm () {
      this.CarddialogVisible = false
      this.initCardList(this.para)
    },
    // 导入会员模板
    member () {
      this.$router.push({name: 'ImportCard'})
    }
  }
}
</script>
<style lang="sass" scoped>
 .top
    display: flex;
    height: .36rem;
    margin-bottom: .3rem;
    .btn
      font-family: PingFangSC-Medium;
      font-size: .15rem;
      color: #20C6BA;
      letter-spacing: 0;
      line-height: .15rem;
      height: .36rem;
      line-height: .36rem;
      border: 1px solid #20C6BA;
      border-radius: .02rem;
      width: 1.32rem;
      text-align: center;
      cursor: pointer;
    .add
      margin-right: .2rem;
    .title
      flex: 1;
      line-height: .36rem;
      span
        background: #E9EDEF;
        border-radius: 100px;
        font-size: .2rem;
        color: #8597A1;
        padding: .03rem .14rem;
        margin-left: .1rem;
    .icon-btn_icon_input:before
      margin-right: .12rem;
</style>




